<template>
    <div id="nav">
      <div class="containerBox">
        <div class="logo"><router-link to="/">MaxDog</router-link>
        </div>
        <button v-on:click="showHideNavMenu" class="hamburger hamburger--collapse" type="button">
          <span class="hamburger-box">
            <span class="hamburger-inner"></span>
          </span>
        </button>
      <div id="mobileMenu" class="closed">
        <a href="#" v-on:click="showHideSubLinksMenu">Услуги
          <div class="dropDownContent hidden">
            <a class="subLink disabled">Послушание </a>
            <a class="subLink disabled">Защита </a>
            <a class="subLink disabled">Социализация собаки </a>
            <a class="subLink disabled">Цирковая дрессировка </a>
            <a class="subLink disabled">Подготовка к выставкам </a>
            <a class="subLink disabled">Помощь и консультации </a>
          </div></a>
        <a href="/#grooming" v-on:click="showHideNavMenu">Груминг</a>
        <router-link to="#" class="disabled">Отзывы</router-link>
        <router-link to="#" class="disabled">Галерея</router-link>
        <router-link @click.native="showHideNavMenu()" to="/philosophy">Наша философия</router-link>
        <a href="/#about" v-on:click="showHideNavMenu">О нас</a>
        <a href="/#footer" v-on:click="showHideNavMenu">Контакты</a>
      </div>
      </div>
    </div>
</template>

<script>
import VueAnchorRouterLink from 'vue-anchor-router-link'
export default {
  name: 'Navigation',
  props: {
    msg: String
  },
  components: {
    VueAnchorRouterLink
  },
  methods: {
    showHideNavMenu (event) {
      const hamburgerIcon = document.querySelector('.hamburger')
      hamburgerIcon.classList.toggle('is-active')
      const mobileMenu = document.getElementById('mobileMenu')
      mobileMenu.classList.toggle('closed')
    },
    showHideSubLinksMenu (event) {
      console.log(`Hiding sublinks`)
      let elementToHide = document.querySelector('.dropDownContent')
      elementToHide.classList.toggle('hidden')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">

$color-white: hsl(0, 0%, 100%);
$color-red: hsl(0, 100%, 41%);
$color-black: hsl(0, 0%, 0%);
$color-lightGray: hsl(0, 0%, 73%);
$color-grayShadow: hsla(0, 0%, 80%, 0.75);
$color-lightGrayBackground: hsla(0, 0%, 95%, 0.75);

$hamburger-layer-width: 30px;
$hamburger-layer-color: $color-red;

@import "../../node_modules/hamburgers/_sass/hamburgers/hamburgers.scss";

#nav {
  background-color: $color-white;
}
.containerBox {
  position: fixed;
  z-index: 1;
  height: 4em;
  width: 100%;
  color: $color-red;
  display: flex;
  justify-content: space-between;
  background-color: $color-white;
  box-shadow: 0px 2px 5px 0px $color-grayShadow;
}
.logo {
  font-family: 'Cinzel', serif;
  font-size: 3em;
  align-self: flex-start;
  padding: .0em 0em 0em .3em;
  a {
    color: $color-red;
    text-decoration: none;
  }
  a:hover {
    color: $color-black;
  }
}

#mobileMenu{
  z-index: 1;
  top: 3.9em;
  overflow: hidden;
  max-height: 40em;
  position: absolute;
  width: 100%;
  text-align: center;
  background-color: $color-white;
  transition-duration: .5s;
  transition-timing-function: linear;
  box-shadow: 0px 3px 5px 0px $color-grayShadow;
  a {
    display: block;
    letter-spacing: .1em;
    text-decoration: none;
    color: $color-black;
    font-size: 1.5em;
    font-weight: 700;
    line-height: 1.5;
    transition-property: color;
    transition-duration: .5s;
    transition-timing-function: cubic-bezier(.94,.27,.37,.97);
  }
  a:hover {
    color: $color-red;
  }
}

#mobileMenu.closed {
  max-height: 0em;
  transition-property: max-height;
}

.dropDownContent {
  background-color: $color-lightGrayBackground;
  font-size: .5em;
  max-height: 18em;
  display: block;
  overflow: hidden;
  transition-duration: .5s;
  transition-timing-function: linear;
}

.dropDownContent.hidden {
  max-height: 0em;
  transition-property: max-height;
}

</style>
